import { ORDER_CREATE_REQUEST, 
    ORDER_CREATE_SUCCESS, 
    ORDER_CREATE_FAIL,  

    ORDER_CREATE_RESET, 

    ORDER_DETAILS_REQUEST,
    ORDER_DETAILS_SUCCESS,
    ORDER_DETAILS_FAIL,

    ORDER_PAY_REQUEST,
    ORDER_PAY_SUCCESS,
    ORDER_PAY_FAIL,
    ORDER_PAY_RESET, 

    ORDER_LIST_MY_REQUEST,
    ORDER_LIST_MY_SUCCESS,
    ORDER_LIST_MY_FAIL,
    ORDER_LIST_MY_RESET,

    ORDERITEM_LIST_REQUEST,
    ORDERITEM_LIST_SUCCESS,
    ORDERITEM_LIST_FAIL,

    ORDERITEM_PAY_REQUEST,
    ORDERITEM_PAY_SUCCESS,
    ORDERITEM_PAY_FAIL,
    ORDERITEM_PAY_RESET, 

} from '../constants/orderConstants' 



export const orderCreateReducer = (state={}, action ) => {
    switch(action.type){
        case ORDER_CREATE_REQUEST:
            return {
                loading: true,
            }

        case ORDER_CREATE_SUCCESS:
            return {
                loading: false, 
                success: true, 
                order: action.payload 
            }
        
        case ORDER_CREATE_FAIL:
            return {
                loading: false, 
                error: action.payload 
            }

        case ORDER_CREATE_RESET:
            return {}

        default:
            return state 
    }
}



export const orderDetailsReducer = (state = {loading:true, orderItems:[], }, action ) => {
    switch(action.type){
        case ORDER_DETAILS_REQUEST:
            return {
                ...state,
                loading: true,
            }

        case ORDER_DETAILS_SUCCESS:
            return {
                loading: false,  
                order: action.payload 
            }
        
        case ORDER_DETAILS_FAIL:
            return {
                loading: false, 
                error: action.payload 
            }

        default:
            return state 
    }
}


